import React from 'react'
import styled from 'styled-components'
import Button from './Button'
import { ThemeProvider } from 'styled-components'
import { dark } from '../styles/Themes'

import img1 from '../assets/images/31.png'
import img2 from '../assets/images/10.png'
import img3 from '../assets/images/3.png'
import img4 from '../assets/images/34.png'
import img5 from '../assets/images/1.png'
import img6 from '../assets/images/35.png'


const Section = styled.section`
width: 100%;
height: 25rem;
position: relative;
border-top: 2px solid ${props => props.theme.text};
border-top: 2px solid ${props => props.theme.text};

background-color: ${props => `rgba(${props.theme.textRgba}, 0.9)`};

display: flex;
justify-content: center;
align-items: center;

overflow: hidden;
`

const ImgContainer = styled.div`
width: 100%;
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);

display: flex;
justify-content: center;
align-items: center;
opacity: 0.2;   

img{
    width: 15rem;
    height: auto;
}
`

const Title = styled.h1`
font-size: ${props => props.theme.fontxxxl};
color: ${props => props.theme.body};
padding: 1rem 2rem;
z-index: 10;
width: 35%
`

const BtnContainer = styled.div`
width: 35$;
display: flex;
justify-content: flex-end;
z-index: 10;
font-size: ${props => props.theme.fontlg};
margin-left: 100px;

button{
    padding: 1.5rem 3rem;
}
`

const Banner = () => {
  return (
    <Section>
        <ImgContainer>
            <img src={img1} alt='NFT' />
            <img src={img2} alt='NFT' />
            <img src={img3} alt='NFT' />
            <img src={img4} alt='NFT' />
            <img src={img5} alt='NFT' />
            <img src={img6} alt='NFT' />
        </ImgContainer>

        <Title> Join The<br />Thrash Hood </Title>
        <BtnContainer>
            <ThemeProvider theme={dark}>
                <Button link={'https://discord.gg/wkTKKWAc'} text='JOIN OUR DISCORD' />
            </ThemeProvider>
        </BtnContainer>
    </Section>
  )
}

export default Banner